import React, { Component } from 'react';
import Slider from 'react-slick';
import './Carousel.css';
import kudus from '../daftar_kost/data_kudus.js';
import pati from '../daftar_kost/data_pati.js';
import jepara from '../daftar_kost/data_jepara.js';
import { useLocation } from "react-router-dom";

export const Carousel = () => {
    const location = useLocation();
    const { pathname } = location;
    const splitlocation = pathname.split('/');
    
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      autoplay: true,
      autoplaySpeed: 2000,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    if(splitlocation[1] === "kudus"){
        return (
            <div>
          <Slider {...settings}>
          {kudus.map((item, index) => (
                    <div
                        key={index}
                        className='carousel-bg'
                    >
                        <div className='carousel-value' 
                        style={{backgroundImage: `url(${item.img1})`}} />
                    </div>
                ))}
          </Slider>
          </div>
        );
    } else if(splitlocation[1] === "pati"){
        return (
            <div>
          <Slider {...settings}>
          {pati.map((item, index) => (
                    <div
                        key={index}
                        className='carousel-bg'
                    >
                        <div className='carousel-value' 
                        style={{backgroundImage: `url(${item.img1})`}} />
                    </div>
                ))}
          </Slider>
          </div>
        );
    } else if(splitlocation[1] === "jepara"){
        return (
            <div>
          <Slider {...settings}>
          {jepara.map((item, index) => (
                    <div
                        key={index}
                        className='carousel-bg'
                    >
                        <div className='carousel-value' 
                        style={{backgroundImage: `url(${item.img1})`}} />
                    </div>
                ))}
          </Slider>
          </div>
        );
    }
    else{
        return (
            <div>
          <Slider {...settings}>
          {kudus.map((item, index) => (
                    <div
                        key={index}
                        className={item.image}
                    >
                        <div className='carousel-value' 
                        style={{backgroundImage: `url(${item.img2})`}} />
                    </div>
                ))}
          </Slider>
          </div>
        );
    }
}
