import React from 'react';
import { Helmet } from 'react-helmet';
import { Navigasi } from './Nav';
import data from './daftar_kost/data_halaman';
import './halaman.css'
import { useLocation, Link } from "react-router-dom";
import { LikeButton } from '@lyket/react';

export const Halaman = () => {
    const location = useLocation();
    const { pathname } = location;
    const splitlocation = pathname.split('/');
    const i = splitlocation[3] - 1;
    const ib = splitlocation[2].indexOf("a");
    const kota = splitlocation[1];
    const inisial = kota.charAt(0)
    const kost = inisial + splitlocation[3];
    const dataCall = data[ib][kota][i][kost];

    return (
        <div>
            <Helmet>
                <style>
                    {`
                .nav-index{
                    height: 51px;
                }
                .Nav-v{
                    display:none;
                }
                `}
                </style>
            </Helmet>
            <Navigasi />
            <div className='halaman-main'></div>
            <div className='daftar'>
                <Link className='offLink'>
                    <div title='Klik untuk melihat semua gambar' className='halaman-img' style={{ backgroundImage: `url(${dataCall.img1})` }}>
                        <div>1/4</div>
                    </div>
                </Link>
                <div className='halaman-detail'>
                    <div className='detail-atas'>
                        <h1 className='halaman-title'>{dataCall.title}</h1>
                        <div className='halaman-like' title='Suka kost ini?'><LikeButton
                            namespace={"sukai-halaman-"+splitlocation[1] + splitlocation[3]}
                            id={splitlocation[1] + splitlocation[3]}
                            component={LikeButton.templates.Twitter}
                            hideCounterIfLessThan={1}
                        /></div>
                    </div>
                </div>
            </div>
        </div>
    );
}
