import './index.css';
import { useLocation } from "react-router-dom";
import { Outlet, Link } from "react-router-dom";
import { useAlert } from 'react-alert';

export const Navigasi = () => {
    const location = useLocation();
    const { pathname } = location;
    const splitlocation = pathname.split('/');
    const alert = useAlert()
    

    return (
        <div className='Nav-main'>
            <div className='Nav-bg'>
                <div>
                    <Link to="/" className='offLink'><section className='Main-icon'>
                        <div className='icon'></div><div>We-Kost</div>
                    </section></Link>
                    <div className='Nav-v'>
                        <Link onClick={function onAlert(){alert.show('Beralih ke daerah Pati')}} className='link-nav' to="/pati"><div className={splitlocation[1] === 'pati' ? "Nav-kota on-kota" : "Nav-kota"}>Pati</div></Link>
                        <Link onClick={function onAlert(){alert.show('Beralih ke daerah Kudus')}} className='link-nav' to="/kudus"><div className={splitlocation[1] === 'kudus' ? "Nav-kota on-kota" : "Nav-kota"}>Kudus</div></Link>
                       <Link onClick={function onAlert(){alert.show('Beralih ke daerah Jepara')}} className='link-nav' to="/jepara"><div className={splitlocation[1] === 'jepara' ? "Nav-kota on-kota" : "Nav-kota"}>Jepara</div></Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

