import React from 'react';
import kudus from '../daftar_kost/data_kudus'
import pati from '../daftar_kost/data_pati'
import jepara from '../daftar_kost/data_jepara'
import { Link, useLocation } from "react-router-dom";

export const Daftar = () => {
    const location = useLocation();
    const { pathname } = location;
    const splitlocation = pathname.split('/');

    if (splitlocation[1] === "kudus") {
        return (
            <div>
                {kudus.map((item, index) => (
                    <div
                        key={index}
                        className='daftar-bg'
                    >
                        <Link className="offLink" to={"/"+splitlocation[1]+"/abc/"+item.no}><div className='daftar-value-main'>
                            <div className="center"><div className='daftar-value'
                                style={{ backgroundImage: `url(${item.img1})` }} /></div>
                            <div className='daftar-detail'>
                                <h3 className='daftar-title'>{item.title}</h3>
                                <section><button>{item.untuk}</button></section>
                            </div>
                        </div>
                            <div className='daftar-selengkapnya'><div></div>
                            </div>
                        </Link>
                    </div>
                ))}
            </div>
        );
    } else if (splitlocation[1] === "pati") {
        return (
            <div>
                {pati.map((item, index) => (
                    <div
                        key={index}
                        className='daftar-bg'
                    ><Link className="offLink" to={"/"+splitlocation[1]+"/bca/"+item.no}><div className='daftar-value-main'>
                            <div className="center"><div className='daftar-value'
                                style={{ backgroundImage: `url(${item.img1})` }} /></div>
                            <div className='daftar-detail'>
                                <h3 className='daftar-title'>{item.title}</h3>
                                <section><button>{item.untuk}</button></section>
                            </div>
                        </div>
                        <div className='daftar-selengkapnya'><div></div></div>
                    </Link>
                    </div>

                ))}
            </div>
        );
    } else if (splitlocation[1] === "jepara") {
        return (
            <div>
                {jepara.map((item, index) => (
                    <div
                        key={index}
                        className='daftar-bg'
                    ><Link className="offLink" to={"/"+splitlocation[1]+"/bac/"+item.no}><div className='daftar-value-main'>
                            <div className="center"><div className='daftar-value'
                                style={{ backgroundImage: `url(${item.img1})` }} /></div>
                            <div className='daftar-detail'>
                                <h3 className='daftar-title'>{item.title}</h3>
                                <section><button>{item.untuk}</button></section>
                            </div>
                        </div>
                        <div className='daftar-selengkapnya'><div></div></div>
                    </Link>
                    </div>

                ))}
            </div>
        );
    }
}